import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Title , Response} from './entity';
import {Params} from '@angular/router';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class TitleService {
  response: Response;
  private titleUrl = '../profTitle.ctl';
  constructor(private http: HttpClient) { }

  /** GET titles from the server */
  getTitles(): Observable<Title[]> {
    return this.http.get<any>(this.titleUrl)
      .pipe(
        catchError(this.handleError<Title[]>('getTitles', []))
      );
  }
  /** GET title by id. Will 404 if id not found */
  getTitle(id: Observable<Params>): Observable<Title> {
    const url = this.titleUrl + '?id=' + id;
    return this.http.get<Title>(url).pipe(
      catchError(this.handleError<Title>(`getTitle id=${id}`))
    );
  }

  /** POST: add a new Title to the server */
  addTitle(title: { no: string; description: string; remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.titleUrl, title, httpOptions).pipe(
      catchError(this.handleError<Response>('addTitle'))
    );
  }

  /** DELETE: delete the Title from the server */
  deleteTitle(title: Title | number): Observable<Response> {
    const id = typeof title === 'number' ? title : title.id;
    return this.http.delete<Response>(this.titleUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteTitle'))
    );
  }

  /** PUT: update the title on the server */
  updateTitle(title: Title): Observable<any> {
    return this.http.put(this.titleUrl , title, httpOptions).pipe(
      catchError(this.handleError<Title>('updateTitle'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}

