import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {Degree, Department, Teacher, Title} from '../entity';
import {TeacherService} from '../teacher.service';
@Component({
  selector: 'app-update-dep',
  templateUrl: './update-tea.component.html',
})
export class UpdateTeaComponent implements OnInit {
  @Input()
  teacher: Teacher;
  departments: Department[];
  degrees: Degree[];
  titles: Title[];
  department: Department;
  degree: Degree;
  title: Title;
  constructor(
    private route: ActivatedRoute,
    private teacherService: TeacherService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getTitles();
    this.getDegrees();//下拉菜单赋值
    this.getDepartments();
    const id = this.route.snapshot.params.id;//要修改专业的id
    this.teacherService.getTea(id)
      .subscribe(res => this.teacher = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  getTitles(): void {
    this.teacherService.getTitles()
      .subscribe(res => this.titles = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }
  save(): void { //多个文本框数据
      this.teacherService.updateTeacher(this.teacher)
      .subscribe((res) => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}
