import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {TeacherService} from '../teacher.service';
import {Degree,  Department, Title , Teacher} from '../entity';
@Component({
  selector: 'app-add-tea',
  templateUrl: './add-tea.component.html'
})
export class AddTeaComponent implements OnInit {
  @Input()  teacher: Teacher;
    departments: Department[];
    degrees: Degree[];
    titles: Title[];
    department: Department;
    degree: Degree;
    title: Title;
    constructor(
    private route: ActivatedRoute,
    private teacherService: TeacherService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getDegrees() ;
    this.getDepartments();
    this.getTitles();
    }
  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }
  getTitles(): void {
    this.teacherService.getTitles()
      .subscribe(res => this.titles = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }
  save(no: string, name: string, degree: Degree, title: Title, department: Department): void { //多个文本框数据
    this.teacher = {id: null, no: no.trim(), name: name.trim(), degree: this.degree, title: this.title, department: this.department};
    if (!name) { return; }
    this.teacherService.addTeacher(this.teacher)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}
